<template>
  <div class="wrapper">
    <div class="content">
      <div class="w-left">
				<div class="c-bg"></div>
				<Map :params="mapParams" class="map" @change='change'></Map>
      </div>
      <div class="w-right">
        <div class="r-content">
          <div class="r-top">
            <div class="t-item" v-for="(item, index) in itemList" :key="index">
              <span :class="['img', item.name === '告警' ? 'jing-bg': item.name === '异常' ? 'yi-bg' : 'ping-bg']"></span>
              <p>{{item.name}}<span :class="item.name === '告警' ? 'jing' : item.name === '异常' ? 'yi' : 'ping'">{{item.value}}</span></p>
            </div>
          </div>
          <ul class="r-title">
            <li v-for="(item, index) in menusList" :key="index">{{item}}</li>
          </ul>
          <ul class="r-inner" v-for="(item, index) in list" :key="index">
            <li>{{item.name}}</li>
            <li>{{item.value}}</li>
            <li>{{Math.abs(item.tb).toFixed(2)}}<i :class="['icon',item.tb < 0 ? 'down-icon':'up-icon']"></i></li>
            <li>{{Math.abs(item.hb).toFixed(2)}}<i :class="['icon',item.hb < 0 ? 'down-icon':'up-icon']"></i></li>
            <li><span :class="['btn', item.status === '告警' ? 'btn-red' : item.status === '异常' ? 'btn-yellow' : 'btn-blue']">{{item.status}}</span></li>
          </ul>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import { get_http } from "@/api/index";
  import ctitle from "@/components/title/index";
  import Map from "@/components/map/Mapyn2";
  export default {
    name: 'yjxs',
    components: {
      ctitle,
      Map
    },
    data() {
      return {
        shengj: '云南省',
        shij: 'invalid',
        list: [],
        itemList: [],
        menusList: ["指数名称", "信访件次", "件次同比", "件次环比", "指数状态"],
        mapParams: {
          canvasStyle: {
            width: "100%",
            height: "98%",
          },
          data: [],
          options: {
            borderColor: '#77608e',
            
            // normalAreaColor: '#649dfb',
            // emphasisAreaColor: '#66e8ff',
            emphasisBorderColor: '#fff',
            emphasis: {
              borderWidth: 2,
              shadowColor: '#1f4ea8',
              shadowBlur: 15,
              fontSize: 16
            },
            scatterColor: '#3cfff8',
            emphasisColor: '#ffd200',
            isHome: false,
            JSON: "530000",
            xName: "name",
						yName: "value",
						top: 0,
            map: {
              "value": "信访量"
            },
            carousel: false
          }
				},
      }
    },
    mounted() {
      this.getMapAPI()
    },
    methods: {
      change(name) {
        this.shij = name;
        this.getAPI(this.year)
      },
      getMapAPI() {
        // 1.地图
        get_http("xfxsyjycYjxsMap", {
          shengj: this.shengj,
          // year: year
        })
        .then(res => {
          this.mapParams.data = res.data.data;
        })
        .catch(err => {
          console.log(err);
        });
        this.getAPI(this.year)
      },
      getAPI(year) {
        // 2.警告、异常、平稳
        get_http("xfxsyjycYjxsItemList", {
          year: year,
          shengj: this.shengj,
          shij: this.shij
        })
        .then(res => {
          this.itemList = res.data.data;
        })
        .catch(err => {
          console.log(err);
        });

        // 3.列表
        get_http("xfxsyjycYjxsList", {
          year: year,
          shengj: this.shengj,
          shij: this.shij
        })
        .then(res => {
          this.list = res.data.data;
        })
        .catch(err => {
          console.log(err);
        });
      }
    },
    computed: {
      year() {
        return this.$store.state.year;
      }
    },
    watch: {
      year(val) {
        this.getAPI(val);
      }
    }
  }
</script>

<style lang="stylus" scoped>
.wrapper
  position relative
  top 0.45rem
  height calc(100% - 45px)
  .content
    width 100%
    height 100%
    display flex
    padding .2rem .3rem
    box-sizing border-box
    .w-left
      position relative
      flex 1
      height 100%
      display flex
      flex-direction column
      .map-chart
        background none
        position absolute
        top -45px
      .c-bg
        position absolute
        width 100%
        height 100%
        z-index 0
        top 0
        left 0
        background url("../../../assets/map-bg.png") no-repeat center bottom
    .w-right
      flex 1
      display flex
      align-items center
      .r-content
        width 8.51rem
        height 5.45rem
        background url("../img/bg5.png") no-repeat center bottom
        .r-top
          height 1.77rem
          padding-top 0.2rem
          display flex
          justify-content space-around
          align-items center
          .t-item
            display flex
            .img 
              width 0.55rem
              height 0.73rem
            .ping-bg
              background url("../img/ping.png") no-repeat center bottom
            .jing-bg
              background url("../img/jing.png") no-repeat center bottom
            .yi-bg
              background url("../img/yi.png") no-repeat center bottom
            p
              font-size 22px
              color #fff
              display flex
              flex-direction column
              justify-content space-around
              text-align center
              margin-left 0.1rem
              span 
                display block
                font-weight bold
              .jing
                color #ff5869
              .yi
                color #f8ce42
              .ping
                color #1a82f7
        .r-title, .r-inner
          display flex
          height 0.6rem
          font-size 18px
          color #fff
          justify-content space-around
          line-height 0.53rem
          padding 0 0.48rem
          li 
            flex 1
            display flex
            justify-content center
            align-items center
            .icon
              display inline-block
              width .4rem
              height .4rem
              margin 0.05rem 0
              margin-right -0.1rem
            .up-icon
              background url("../img/up-icon.png") no-repeat center
            .down-icon
              // margin-right 0
              background url("../img/down-icon.png") no-repeat center
            .btn
              width .6rem
              line-height .3rem
              text-align center
              font-size .14rem
              border-radius 4px
              margin 0.1rem auto
              &.btn-blue
                color #fff
                background-color #0b598e
                border 2px solid #14a4f1
              &.btn-yellow
                color #f9db3b
                background-color #765b31
                border 2px solid #ffb435
              &.btn-red
                color #e9ebfc
                background-color #6b2f40
                border 2px solid #e95659
        .r-inner
          color 
</style>
